import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {UserList} from './page/UserList'
// import {UnverifiedMemberList} from './page/UnverifiedMemberList'
import {DetailUser} from './page/DetailUser'
import {EditUser} from './page/EditUser'
// import {DetailUnverifiedMember} from './page/DetailUnverifiedMember'
// import {EditVerifiedMember} from './page/EditVerifiedMember'
import UserScreens from './Screens'

const UserRoutes: React.FC = () => {
  return (
    <Switch>
      <Route path={UserScreens.USER_LIST.PATH} component={UserList} />
      <Route path={UserScreens.DETAIL_USER.PATH} component={DetailUser} />
      <Route path={UserScreens.Edit_USER.PATH} component={EditUser} />
      <Redirect from='/user' exact={true} to={UserScreens.USER_LIST.PATH} />
      <Redirect to={UserScreens.USER_LIST.PATH} />
    </Switch>
  )
}

export default UserRoutes
